export default {
	data() {
		return {

			type: "password",
			isuser: 0, //用户名是否占用
			isemail: 0, //邮箱是否占位
			show: false, // 是否显示密码
			invalid: false, // 邮箱是否符合
			employ: false, //是否被占用
			showurl: '../../static/images/register/showin.png',
			email: '',
			name: '',
			password: '',
			check_password: '',
			checkshow: false
		};
	},
	methods: {
		//密码是否显隐
		showon: function() {
			if (this.show) {
				this.type = 'password';
				this.show = !this.show;
				this.showurl = '../../static/images/register/showin.png';
			} else {
				this.type = 'text';
				this.show = !this.show;
				this.showurl = '../../static/images/register/showon.png';
			}
		},
		isname: function() {
			if (this.name.length > 0) {
				this.isuser = true;
			} else {
				this.isuser = false;
			}
		},
		goLogin: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		checknumber() {
			var reg = /^[A-Za-z0-9]{6,20}$/;
			if (this.password === this.check_password && reg.test(this.password)) {
				this.checkshow = false
			} else {
				this.checkshow = true
			}
		},
		check() {

			if (this.check_password.length > 0) {
				var reg = /^[A-Za-z0-9]{6,20}$/;

				if (this.password === this.check_password && reg.test(this.password)) {
					this.checkshow = false;



				} else {
					this.checkshow = true

				}


			}
		},
		submit() {

			if (this.checkshow === false && this.check_password.length > 0) {

				uniCloud.callFunction({
					name: 'register',
					data: {
						user_id: this.name,
						password: this.password
					},

					success(res) {
						console.log(res)
					}
				})





			} else {
				//弹一个提示框
				uni.showModal({
					title: '提示',
					content: '请输入正确的账号和密码',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
				this.checkshow = true
			}

		}
	}
};
